body {
  font-family: 'Roboto', sans-serif;
  font-family: 'Roboto', sans-serif;
  background-image: url('night.jpeg');
  background-size: cover;
  background-position: center;
  color: antiquewhite;
}



.App {
  text-align: center;
  margin-top: 50px;
}

h1 {
  font-size: 24px;
  margin-bottom: 20px;
}

input {
  padding: 10px;
  margin-bottom: 10px;
}

button {
  padding: 10px 20px;
  margin-bottom: 10px;
  background-color: #4caf50;
  color: white;
  border: none;
  cursor: pointer;
}

button:hover {
  background-color: #45a049;
}

img {
  width: 100px;
  height: auto;
  margin-bottom: 10px;
}

h2 {
  font-size: 18px;
  margin-bottom: 10px;
}

p {
  color: red;
  font-size: 16px;
}

